.footer{
    position: fixed;
    display: flex;
    justify-content: space-between;
    bottom:0;
    height: 55px;
    width:100%;
    background-color: #282828;
    padding:20px;
}

.footer__right * .MuiSlider-root{

    color: green;
}
.MuiSlider-root{
    size:0px;
}

.footer__left{
    flex: 0.3;
    display: flex;
    align-items: center;
    color: white;
    width:300px;


}
.footer__icon:hover,
.footer__blue:hover {
  transition: transform 0.2s ease-in-out;
  transform: scale(1.2) !important;
}




.footer__albumLogo{
    height: 60px;
    width: 60px;
    margin-right: 20px;
    object-fit: contain;
}

.footer__blue{
    color:green;
}
.footer__songInfo > h4
{
    margin-bottom:5px;
}
.footer__songInfo  > p{
    font-size: 12px;

}

.footer__center{
    flex: 0.4;
    padding: 0 100px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 300px;


}
.footer__right{
    flex: 0.3;
    display: flex;
    color: white;
    justify-content: space-between;
    align-items: center;

}
